import { PageInterface } from './menu';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Nav } from 'ionic-angular';
import { AidePage } from '../aide/aide';
import { ScreenOrientation } from '@ionic-native/screen-orientation';

export interface PageInterface {
  title: string;
  pageName: string;
  tabComponent?: any;
  index?: number;
  icon: string;
  no?: number;
}

@IonicPage()
@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {

  rootPage = "TabsPage";

  @ViewChild(Nav) nav: Nav;

  pages: PageInterface[] = [
    { title: 'Accueil', pageName: 'TabsPage', tabComponent: 'AccueilPage', index: 1, icon: 'home' },
    { title: 'Historique', pageName: 'TabsPage', tabComponent: 'HistoriquePage', index: 0, icon: 'folder-open' },
    { title: 'Paramètres', pageName: 'TabsPage', tabComponent: 'ParametrePage', index: 2, icon: 'settings' }

  ]

  constructor( public navCtrl: NavController,public navParams: NavParams, public sc: ScreenOrientation) {
    this.sc.lock(this.sc.ORIENTATIONS.PORTRAIT);
  }


  openPage(page: PageInterface) {
    let params = {};

    if (page.index) {
      params = { tabIndex: page.index };
    }

    if (this.nav.getActiveChildNav() && page.index != undefined) {
      this.nav.getActiveChildNav().select(page.index);
    } else {
      this.nav.setRoot(page.pageName, params);
    }
  }

  isActive(page: PageInterface) {
    let childNav = this.nav.getActiveChildNav();

    if (childNav) {
      if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
        return 'danger';
      }
      return 'blue';
    }

    if (this.nav.getActive() && this.nav.getActive().name === page.pageName) {
      return 'danger';
    }

  }

  aide() {
    this.navCtrl.push(AidePage);
  }
}



