import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ScreenOrientation } from '@ionic-native/screen-orientation';
/**
 * Generated class for the AidePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-aide',
  templateUrl: 'aide.html',
})
export class AidePage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public sc: ScreenOrientation) {
    this.sc.lock(this.sc.ORIENTATIONS.PORTRAIT);
  }  

  ionViewDidLoad() {
    console.log('ionViewDidLoad AidePage');
  }

}



